.loader-container {
  width: 100%;
  justify-content: center;
  align-items: center;
  animation: animate .9s linear infinite;
  display: flex;
  position: relative;
}

.loader-box {
  width: 200px;
  height: 200px;
  background-color: #fee8d8;
  border-radius: 50%;
  position: relative;
}

.loader-box:before {
  content: "";
  width: 50%;
  height: 100%;
  background: linear-gradient(to top, rgba(0, 0, 0, 0), #aa3202) 0 0 / 100px 180px no-repeat;
  border-top-left-radius: 100px;
  border-bottom-left-radius: 100px;
  position: absolute;
  top: 0;
  left: 0;
}

.loader-box:after {
  content: "";
  width: 20px;
  height: 20px;
  z-index: 15;
  background-color: #ff6b01;
  border-radius: 50%;
  position: absolute;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
  box-shadow: 0 0 10px #ff6b01, 0 0 20px #ff6b01, 0 0 30px #ff6b01, 0 0 40px #ff6b01, 0 0 50px #ff6b01, 0 0 60px #ff6b01, 0 0 70px #ff6b01, 0 0 80px #ff6b01, 0 0 90px #ff6b01, 0 0 100px #ff6b01;
}

.loader-box__elem {
  background-color: #fff;
  border-radius: 50%;
  position: absolute;
  top: 20px;
  bottom: 20px;
  left: 20px;
  right: 20px;
}

@keyframes animate {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

